import React from "react";
import { Link } from "react-router-dom";

import "./IntroScreen.css";
import "./fonts/fonts.css";
import MultiAnswerTypo from "../MultiAnswerTypo";

export default class IntroScreen extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            continueAllowed: false,
            fixedName: "",
        };

        this.onTypoFixed = this.onTypoFixed.bind(this);
    }

    onTypoFixed(answer) {
        this.setState({
            continueAllowed: true,
            fixedName: answer,
        });
        this.props.onIntroFixed(answer);
    }

    render() {
        var continueDiv = null;
        if (this.state.continueAllowed && this.state.fixedName === "Stame") {
            continueDiv = (
                <div>
                    <p>
                        Wow, Cambria. The skill you demonstrated in fixing that typo was <i>truly</i> inspiring.
                    </p>
                    <br/>
                    <p>
                        So inspiring, in fact, that we'd
                        like to offer you a job: effective today, you are the
                        sole editor of every website on the planet.
                    </p>
                    <br/>
                    <p>
                        Congrats on accepting your new position! Your work
                        begins now: we already have plenty of articles for you
                        to fix up.
                    </p>
                    <br/>
                    <div className="linkContainer">
                        <Link to="dashboard">Go to Dashboard</Link>
                    </div>
                </div>
            );
        } else if (
            this.state.continueAllowed &&
            this.state.fixedName === "Pauls"
        ) {
            continueDiv = (
                <div>
                    <p>Good to see you again.</p>
                    <br/>
                    <div className="linkContainer">
                        <Link to="dashboard">Go to Dashboard</Link>
                    </div>
                </div>
            );
        }
        return (
            <div className="intro">
                <h1>
                    Welcome, Cambria{" "}
                    <MultiAnswerTypo
                        answers={["Stame", "Pauls"]}
                        onFixed={this.onTypoFixed}
                    >
                        Satme
                    </MultiAnswerTypo>
                    !
                </h1>
                <div className="content">
                    <p>
                        Uh oh. Looks like we misspelled your name. Go ahead and
                        correct it.
                    </p>
                    <br/>
                    <br/>
                    {continueDiv}
                    <br/>
                    <br/>
                </div>
            </div>
        );
    }
}
