import React from "react";
import { Link } from "react-router-dom";

import "./DomesticArticle.css";
import "./fonts/fonts.css";
import Typo from "../Typo";

import bread_1 from "./images/bread_1.jpg";
import bread_2 from "./images/bread_2.jpg";
import crochet_1 from "./images/crochet_1.jpg";
import crochet_2 from "./images/crochet_2.jpg";
import haircut from "./images/haircut.jpg";
import shotgun from "./images/shotgun.jpg";

import wheat from "./icons/wheat.png";

export default class DomesticArticle extends React.Component {
    constructor(props) {
        super(props);

        this.typos = {
            newly: {
                default: "Nooly",
                answer: "Newly",
            },
            improved: {
                default: "improvised",
                answer: "improved",
            },
            bake: {
                default: "Bark",
                answer: "Bake",
            },
            gorging: {
                default: "forging",
                answer: "gorging",
            },
            never: {
                default: "nerver",
                answer: "never",
            },
            passed: {
                default: "pasded",
                answer: "passed",
            },
            crochet: {
                default: "croshay",
                answer: "crochet",
            },
            inexplicably: {
                default: "inexplaincably",
                answer: "inexplicably",
            },
            knit: {
                default: "kit",
                answer: "knit",
            },
            stylist: {
                default: "stylsist",
                answer: "stylist",
            },
            eyebrows: {
                default: "eyebrowns",
                answer: "eyebrows",
            },
            quarantine: {
                default: "quartertine",
                answer: "quarantine",
            },
            shoot: {
                default: "shook",
                answer: "shoot",
            },
            lavender: {
                default: "lavandar",
                answer: "lavender",
            },
            updated: {
                default: "updaited",
                answer: "updated",
            },
        };

        this.state = {
            typosLeft: Object.keys(this.typos).length,
        };

        this.typoFactory = this.typoFactory.bind(this);
        this.typoFixed = this.typoFixed.bind(this);
    }

    typoFactory(typoObject) {
        var startText = this.props.typosOmitted
            ? typoObject.answer
            : typoObject.default;

        return (
            <Typo answer={typoObject.answer} onFixed={this.typoFixed}>
                {startText}
            </Typo>
        );
    }

    typoFixed() {
        var typosLeft = this.props.typosOmitted ? 0 : this.state.typosLeft - 1;
        this.setState({
            typosLeft: typosLeft,
        });
    }

    componentDidUpdate(prevProps, prevState) {
        if (
            this.state.typosLeft === 0 &&
            this.state.typosLeft !== prevState.typosLeft &&
            this.props.onAllTyposFixed
        ) {
            this.props.onAllTyposFixed(this.props.articleId);
        }
    }

    render() {
        return (
            <div
                className="home article"
                style={{ backgroundColor: "lavender" }}
            >
                <div className="home mockSite">
                    <div className="home topBanner">
                        <div className="home siteTitleContainer">
                            <img src={wheat} alt="wheat" />
                            <div className="home siteTitle">
                                Homestead Living Company
                            </div>
                            <div className="home siteSubtitle">
                                <span className="home sitesubtitleText">
                                    Churn your own independence.
                                </span>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="home articleColumn">
                    <div className="home articleHeader">
                        <div className="home articleTitle">
                            <h1>
                                {this.typoFactory(this.typos.newly)} Stuck
                                Inside? Challenge Your Inner Domestic
                            </h1>
                        </div>
                        <div className="home articleSubtitle">
                            <h2>
                                Here are 3 ways to rock your quarantine life at
                                home, as demonstrated by Cambria Stame.
                            </h2>
                        </div>
                    </div>

                    <hr />

                    <div className="home articleContent">
                        <p>
                            If I’ve learned anything at all from the
                            coronavirus, it’s that being cooped up inside is the
                            toughest part of a pandemic. Homestead Living
                            recently sent me to visit Cambria Stame in Boise,
                            Idaho, who showed me three skills she’s been
                            learning to fight the quarantine blues.
                        </p>
                        <div style={{ clear: "both" }}></div>
                        <h3>
                            Activity #1:{" "}
                            <u>
                                {this.typoFactory(this.typos.bake)} bread to
                                last you a lifetime.
                            </u>
                        </h3>
                        <div style={{ clear: "both" }}></div>
                        <div className="home rightImage">
                            <img
                                className="home image"
                                src={bread_1}
                                alt="bread 1"
                            />
                            <img
                                className="home image"
                                src={bread_2}
                                alt="bread 2"
                            />
                            <div className="home imageCaption">
                                <div className="home imageCaptionText">
                                    Cambria's baking technique has{" "}
                                    {this.typoFactory(this.typos.improved)}{" "}
                                    significantly since her first loaf, shown in
                                    the top picture.
                                </div>
                            </div>
                        </div>
                        <p>
                            Sourdough bread is the nectar of the gluten gods.
                            Had a sourdough bread tree been placed in the Garden
                            of Eden, Adam and Eve would have been too busy{" "}
                            {this.typoFactory(this.typos.gorging)} themselves on
                            its fruits to plunge the world into sin. What better
                            way to spend time inside than to learn the art of
                            baking sourdough to perfection?
                        </p>
                        <p>
                            Of course, as Cambria taught me, sourdough isn’t the
                            easiest bread to make. “Each little step matters,”
                            she noted, “from the feeding of the starter to the
                            way the dough is kneaded. The bread must be cared
                            for every step of the way, much like a child.”
                            Cambria was quick to assure me, however, that to the
                            best of her knowledge she has{" "}
                            {this.typoFactory(this.typos.never)} before baked a
                            child on accident.
                        </p>
                        <p>
                            But what makes sourdough last a lifetime? The secret
                            is the starter, a living ingredient used as the
                            basis for every single loaf of sourdough bread.
                            Though Cambria received her starter from her aunt,
                            starters can be{" "}
                            {this.typoFactory(this.typos.passed)} down through
                            many generations as long as they are continually fed
                            and cared for. “The value of a starter cannot be
                            understated,” said Cambria. “If my house was burning
                            down and I had the choice of saving either a litter
                            of newborn puppies or my bread starter, I would—”
                            but she was cut off by the ringing of the oven
                            timer.
                        </p>
                        <p>
                            If you’re after a hobby to tackle while staying at
                            home, look no further than making your kitchen a
                            bakery of sourdough bread. There are few hobbies
                            near as delicious.
                        </p>
                        <div style={{ clear: "both" }}></div>
                        <h3>
                            Activity #2: <u>Crochet for your kids.</u>
                        </h3>
                        <div style={{ clear: "both" }}></div>
                        <div className="home leftImage">
                            <img
                                className="home image"
                                src={crochet_1}
                                alt="crochet 1"
                            />
                            <img
                                className="home image"
                                src={crochet_2}
                                alt="crochet 2"
                            />
                            <div className="home imageCaption">
                                <div className="home imageCaptionText">
                                    Just as with sourdough, practice makes
                                    perfect when it comes to crocheting.
                                </div>
                            </div>
                        </div>
                        <p>
                            I want to be clear: crocheting is not for the faint
                            of heart. A test of patience, endurance, and fine
                            motor skills, crocheting tough to learn, and even
                            tougher to master. When boredom seized hold of her
                            life, however, Cambria turned to crocheting to fill
                            the empty hours of her day.
                        </p>
                        <p>
                            Consistency with the fundamentals is the key to
                            success with yarn, Cambria says. “There’s the single{" "}
                            {this.typoFactory(this.typos.crochet)}, the magic
                            circle, the double crochet, the half-double crochet,
                            the triple-double crochet, the single-but-looking
                            crochet, the it’s-complicated crochet, the
                            octuple-backflip crochet…” Once you master these
                            basic stitches, however, the world becomes your
                            oyster. “I can crochet anything,” Cambria says, “as
                            long as I have enough alpacas to harvest yarn from.”
                            She later revealed that her current project is a
                            crochet fighter jet for the U.S. Air Force.
                        </p>
                        <p>
                            One pitfall that many beginners run into is the
                            temptation to become a pirate. Using a crochet hook
                            for too long can make you want to do daily household
                            tasks with the hook rather than your hands. Soon
                            enough, you’ll be sprouting a peg-leg, drinking huge
                            amounts of rum, and parrots will{" "}
                            {this.typoFactory(this.typos.inexplicably)} land on
                            your shoulder. When asked if she had ever come close
                            to becoming a pirate, Cambria replied,
                            “Yaarrrrrrrrrrn!”
                        </p>
                        <p>
                            The rewards of crocheting are well worth the time
                            spent mastering the craft. I find this to be a
                            perfect hobby for this pandemic season not just
                            because it consumes time, but also because soon all
                            the department stores will be closed and you will
                            have no other choice but to{" "}
                            {this.typoFactory(this.typos.knit)} your own
                            clothing from scratch.
                        </p>
                        <div style={{ clear: "both" }}></div>
                        <h3>
                            Activity #3:{" "}
                            <u>
                                Become a home{" "}
                                {this.typoFactory(this.typos.stylist)}.
                            </u>
                        </h3>
                        <p>
                            Speaking of closures, when was the last time you
                            were able to go out and get a haircut? A trim seems
                            so simple to get, and yet these days no businesses
                            are open to do it. Cambria, however, came up with a
                            solution: she learned to cut hair herself!
                        </p>
                        <div style={{ clear: "both" }}></div>
                        <div className="home rightImage">
                            <img
                                className="home image"
                                src={haircut}
                                alt="haircut"
                            />
                            <div className="home imageCaption">
                                <div className="home imageCaptionText">
                                    Cambria's attention to detail with her
                                    clients is surgical.
                                </div>
                            </div>
                        </div>
                        <p>
                            To learn to cut hair, you’ll need a guinea pig to
                            volunteer their locks for your educational benefit.
                            “The person I found wasn’t too enthusiastic at
                            first,” Cambria told me, “But that all changed once
                            the sedative kicked in.” With some guidance from her
                            sister, Cambria quickly learned how fun using the
                            oscillating blades of trimming death can be. “It
                            doesn’t have to just be the hair on top of their
                            head! I’m about to take his{" "}
                            {this.typoFactory(this.typos.eyebrows)} away too.”
                            When you cut hair at home, however, make sure your
                            volunteer doesn’t wake up from the sedative while
                            you’re shaving off their eyelashes, as Cambria’s
                            volunteer once did while I was watching. Always have
                            an extra shot of tranquilizer ready, just in case.
                        </p>
                        <p>
                            If you care about your own hygiene as well as that
                            of those around you, cutting hair is definitely a{" "}
                            {this.typoFactory(this.typos.quarantine)} skill you
                            should pick up. If you live in a big city where
                            haircuts cost an arm and a leg, learning to trim
                            hair could also save you a pretty penny. And if
                            you’re the type of person who likes to collect hair,
                            this skill could fuel your weird and depraved
                            hoarding tendencies.
                        </p>
                        <div style={{ clear: "both" }}></div>
                        <h3>
                            Bonus: <u>Exercise your rights.</u>
                        </h3>
                        <div className="home bigImage">
                            <img
                                className="home image"
                                src={shotgun}
                                alt="shotgun"
                            />
                            <div className="home imageCaption">
                                <div className="home imageCaptionText">
                                    This girl's got some bite to her.
                                </div>
                            </div>
                        </div>
                        <p>
                            When you spend all your time building up your
                            homestead, it can be easy to neglect preparing to
                            defend it. When the government inevitably turns on
                            its citizens, Cambria Stame will be ready. A natural
                            sharpshooter, Cambria demonstrated for me how she
                            can hit a dime from 200 yards with a hunting rifle.
                            “It’s easy, really. Just point and{" "}
                            {this.typoFactory(this.typos.shoot)},” she said with
                            little hesitation as she knocked a clay pigeon out
                            of the sky behind her back with a double-barrel
                            shotgun. No matter who you are, learning to use a
                            gun is a must for defending your land.
                        </p>
                        <p>
                            If you’re looking for things to do in the midst of a
                            pandemic, look no further than bread baking,
                            crocheting, hair trimming, and shooting as great
                            ways to spend your time. If you’re up for a real
                            challenge, try to combine them all at once! Namely,
                            shoot the hair off of a volunteer’s head with a gun
                            you knitted yourself using sourdough bread bullets.
                            The sky is truly the limit on your homestead.
                        </p>
                        <hr />
                        <br />
                        <div className="home articleAttribution">
                            <p>
                                <i>
                                    Jane Taylor is a Senior Editor at Homestead
                                    Living Company. When she isn't publishing
                                    articles or copy-editing, you can find her
                                    tending to her apple orchard and{" "}
                                    {this.typoFactory(this.typos.lavender)}{" "}
                                    fields.
                                    <br />
                                    <br />
                                    Originally published 4/29/2020; last{" "}
                                    {this.typoFactory(this.typos.updated)}{" "}
                                    6/22/2020.
                                </i>
                            </p>
                        </div>
                    </div>
                </div>

                <div className="home articleFooter">
                    <div className="home articleCopyright">
                        <p>
                            Copyright &copy; 2020 Homestead Living Company. All
                            Rights Reserved.
                        </p>
                    </div>
                    <div className="home articleTyposLeft">
                        <p>
                            {this.state.typosLeft === 0
                                ? "Article finished!"
                                : "Keep searching."}{" "}
                            Typos Remaining: {this.state.typosLeft}
                        </p>
                    </div>
                    <Link to="/dashboard" onClick={() => window.scrollTo(0, 0)}>
                        Return to Dashboard
                    </Link>
                </div>
            </div>
        );
    }
}
