import React from "react";
import { Link } from "react-router-dom";

import "./NewYearArticle.css";
import "./fonts/fonts.css";
import Typo from "../Typo";

import polaroid from "./images/polaroid.jpg";
import new_year_2019 from "./images/new_year_2019.jpg";
import new_year_2020 from "./images/new_year_2020.jpg";
import new_year_balloons from "./images/new_year_balloons.jpg";

export default class NewYearArticle extends React.Component {
    constructor(props) {
        super(props);

        this.typos = {
            complaints: {
                default: "compilens",
                answer: "complaints",
            },
            subdivision: {
                default: "sudvibsiion",
                answer: "subdivision",
            },
            agriculture: {
                default: "agarniculture",
                answer: "agriculture",
            },
            outnumber: {
                default: "outlunger",
                answer: "outnumber",
            },
            year2019: {
                default: "3019",
                answer: "2019",
            },
            grocery: {
                default: "gronkery",
                answer: "grocery",
            },
            glitter: {
                default: "gtiller",
                answer: "glitter",
            },
            oklahoma: {
                default: "oaklahome",
                answer: "Oklahoma",
            },
            idaho: {
                default: "Iowa",
                answer: "Idaho",
            },
            rights: {
                default: "lefts",
                answer: "rights",
            },
        };

        this.state = {
            typosLeft: Object.keys(this.typos).length,
        };

        this.typoFactory = this.typoFactory.bind(this);
        this.typoFixed = this.typoFixed.bind(this);
    }

    typoFactory(typoObject) {
        var startText = this.props.typosOmitted
            ? typoObject.answer
            : typoObject.default;

        return (
            <Typo answer={typoObject.answer} onFixed={this.typoFixed}>
                {startText}
            </Typo>
        );
    }

    typoFixed() {
        var typosLeft = this.props.typosOmitted ? 0 : this.state.typosLeft - 1;
        this.setState({
            typosLeft: typosLeft,
        });
    }

    componentDidUpdate(prevProps, prevState) {
        if (
            this.state.typosLeft === 0 &&
            this.state.typosLeft !== prevState.typosLeft &&
            this.props.onAllTyposFixed
        ) {
            this.props.onAllTyposFixed(this.props.articleId);
        }
    }

    render() {
        return (
            <div
                className="newyear article"
                style={{ backgroundColor: "floralwhite" }}
            >
                <div className="newyear mockSite">
                    <div className="newyear topBanner">
                        <div className="newyear siteTitle">
                            The Polar News Bearer
                        </div>
                        <div className="newyear siteSubtitle">
                            Stories worth launching fireworks over.
                        </div>
                    </div>
                </div>

                <div className="newyear articleHeader">
                    <div className="newyear articleTitle">
                        <h1>
                            Californians Throw Raucous New Year’s Party For
                            Second Straight Year
                        </h1>
                    </div>
                    <div className="newyear articleSubtitle">
                        <h2>
                            Idaho neighbors are filing noise{" "}
                            {this.typoFactory(this.typos.complaints)} after the
                            wild bash shattered state records.
                        </h2>
                    </div>
                    <div className="newyear articleAttribution">
                        <p>
                            <i>by Grettel Pretzel | January 2nd, 2020</i>
                        </p>
                    </div>
                    <div className="newyear doubleImageContainer">
                        <img
                            className="newyear doubleImageTop"
                            src={new_year_2019}
                            alt="new year 2019"
                        />
                        <img
                            className="newyear doubleImageBottom"
                            src={new_year_2020}
                            alt="new year 2020"
                        />
                    </div>
                </div>

                <div className="newyear articleContent">
                    <hr />
                    <p>
                        The police department of Eagle, Idaho was flooded with
                        calls yesterday after a record-breaking party took place
                        in the Island Woods{" "}
                        {this.typoFactory(this.typos.subdivision)}. The party
                        was said to be the largest celebration of a new year to
                        have ever taken place in the state, with over 10 people
                        gathered in a single house at once. A nearby neighbor
                        recounted the event to PNB: “They were speaking very
                        jovially, counting down numbers very loudly, and
                        launching off explosives in their backyard.” Photographs
                        allegedly taken inside the party show large amounts of
                        balloons, sparklers, and glitter present.
                    </p>
                    <img
                        className="newyear rightImage"
                        src={new_year_balloons}
                        alt="new year balloons"
                    />
                    <p>
                        The wild gathering comes as a huge surprise to many
                        Idahoans, who never really go to great lengths to
                        celebrate anything unrelated to{" "}
                        {this.typoFactory(this.typos.agriculture)} or ranching.
                        “The last time I saw more than four people in a single
                        room was back in my barn when ol’ Bessy was givin’
                        birth,” said local dairy farmer Buck Wickleskin,
                        gesturing toward one of his cows, “and that was only
                        ‘cause it was a breech, so the vet and two assistants
                        were there.” Like Wickleskin, most Idaho residents do
                        not like situations in which nearby animals or tons of
                        produce do not {this.typoFactory(this.typos.outnumber)}{" "}
                        people by at least tenfold. When asked what he thought
                        about the recent party in Eagle, Buck merely replied, “I
                        felt it in my knee that night. I always feel the evil in
                        my knee.”
                    </p>
                    <p>
                        The individuals responsible for the New Year’s gathering
                        are members of the Stame family, who recently moved from
                        California to Idaho in search of lower taxes. They first
                        made headlines for their{" "}
                        {this.typoFactory(this.typos.year2019)} New Year’s
                        party, and then again that year when they failed to show
                        up for the potato harvest. “It’s a shame, really,” said
                        Wade Byron, head of the potato harvest. “It used to be
                        that you work for what you eat in this country. Now
                        these Californians are fattening themselves off{" "}
                        {this.typoFactory(this.typos.grocery)} stores. The
                        nerve.” The recent influx of Californians into the state
                        has many Idahoans wondering how the roads will handle
                        the new drivers. A report released by the Idaho Potato
                        Commission revealed that vehicle density on the roads is
                        up 400% from last year, with nearly 8 cars per mile of
                        interstate on average. Tractor accidents have also risen
                        in frequency.
                    </p>
                    <img
                        className="newyear leftImage"
                        src={polaroid}
                        alt="polaroid"
                    />
                    <p>
                        Police are currently seeking to interview Cambria Stame,
                        one of the individuals responsible for the party. An
                        insider at the department told PNB that “they believe
                        she may be involved in terrorist activities and/or the
                        glitter trade based on photographs from the party.” One
                        photo retrieved from the party shows her lying down on a
                        floor full of {this.typoFactory(this.typos.glitter)}, an
                        especially worrying sight for neighbors in the once
                        peaceful Island Woods subdivision. Glitter was banned
                        from the state of Idaho in 1904, and police have been
                        waging a war on the glitter black market ever since.
                        Harlow Sanderstash, an expert on glitter, believes the
                        Stames may be involved in low-level glitter
                        distribution. “The glitter shown in the photographs is
                        coarse, not the same high-quality glitter as one might
                        find in larger cities.” Super-fine glitter is not common
                        in rural areas; the last major rural glitter incident
                        took place in 1989 when a trailer secretly hauling three
                        tons of glitter into{" "}
                        {this.typoFactory(this.typos.oklahoma)} overturned on
                        the highway. Wind carried microscopic glitter particles
                        to locations over 30 miles away.
                    </p>
                    <p>
                        {this.typoFactory(this.typos.idaho)} Governor Brad
                        Little is expected to release a statement later today to
                        quell the growing anxiety about the wild and
                        unpredictable parties hosted by the Stames. Little,
                        whose most recent campaign was built on restoring
                        confidence in law and order after a Patriots fan was
                        spotted in downtown Boise, will most likely draw a hard
                        line against the gatherings at the Stame house to ensure
                        support from his constituents. Residents question,
                        however, whether even he has the power to control the
                        lawlessness demonstrated on New Year’s Day 2020.
                    </p>
                    <hr />
                    <p className="newyear finalFlourish">~</p>
                </div>

                <div className="newyear articleFooter">
                    <div className="newyear articleCopyright">
                        <p>
                            Copyright &copy; 2020{" "}
                            <span style={{ fontFamily: "Notable Regular" }}>
                                The Polar News Bearer
                            </span>
                            . All {this.typoFactory(this.typos.rights)}{" "}
                            reserved.
                        </p>
                    </div>
                    <div className="newyear articleTyposLeft">
                        <p>
                            {this.state.typosLeft === 0
                                ? "Article finished!"
                                : "Keep searching."}{" "}
                            Typos Remaining: {this.state.typosLeft}
                        </p>
                    </div>
                    <Link to="/dashboard" onClick={() => window.scrollTo(0, 0)}>
                        Return to Dashboard
                    </Link>
                </div>
            </div>
        );
    }
}
