import React from "react";
import {
    HashRouter as Router,
    Switch,
    Route,
    Link,
    Redirect,
} from "react-router-dom";

import IntroScreen from "./dashboard/IntroScreen";
import Dashboard from "./dashboard/Dashboard";

import NewYearArticle from "./new_year/NewYearArticle";
import CaliforniaArticle from "./california/CaliforniaArticle";
import DiamondArticle from "./diamond/DiamondArticle";
import DomesticArticle from "./domestic/DomesticArticle";
import Finale from "./finale/Finale";

export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            playGame: true,
            completedArticles: [],
        };

        this.setModeByName = this.setModeByName.bind(this);
        this.onArticleFixed = this.onArticleFixed.bind(this);
    }

    setModeByName(name) {
        this.setState({
            playGame: !(name === "Pauls"),
        });
    }

    onArticleFixed(articleId) {
        if (!this.state.completedArticles.includes(articleId)) {
            this.setState({
                completedArticles: this.state.completedArticles.concat(
                    articleId
                ),
            });
        }
    }

    render() {
        return (
            <Router>
                {/* A <Switch> looks through its children <Route>s and
                    renders the first one that matches the current URL. */}
                <Switch>
                    <Route path="/dashboard">
                        <Dashboard
                            completedArticles={this.state.completedArticles}
                            playGame={this.state.playGame}
                        />
                    </Route>
                    <Route path="/newyear">
                        <NewYearArticle
                            articleId="newyear"
                            onAllTyposFixed={this.onArticleFixed}
                            typosOmitted={this.state.completedArticles.includes(
                                "newyear"
                            ) || !this.state.playGame}
                        />
                    </Route>
                    <Route path="/california">
                        <CaliforniaArticle
                            articleId="california"
                            onAllTyposFixed={this.onArticleFixed}
                            typosOmitted={this.state.completedArticles.includes(
                                "california"
                            ) || !this.state.playGame}
                        />
                    </Route>
                    <Route path="/diamond">
                        <DiamondArticle
                            articleId="diamond"
                            onAllTyposFixed={this.onArticleFixed}
                            typosOmitted={this.state.completedArticles.includes(
                                "diamond"
                            ) || !this.state.playGame}
                        />
                    </Route>
                    <Route path="/domestic">
                        <DomesticArticle
                            articleId="domestic"
                            onAllTyposFixed={this.onArticleFixed}
                            typosOmitted={this.state.completedArticles.includes(
                                "domestic"
                            ) || !this.state.playGame}
                        />
                    </Route>
                    <Route path="/finale">
                        <Finale
                            articleId="finale"
                            onAllTyposFixed={this.onArticleFixed}
                            typosOmitted={this.state.completedArticles.includes(
                                "finale"
                            ) || !this.state.playGame}
                        />
                    </Route>
                    <Route path="/">
                        <IntroScreen onIntroFixed={this.setModeByName} />
                    </Route>
                </Switch>
            </Router>
        );
    }
}
