import React from "react";
import { Link } from "react-router-dom";

import "./DiamondArticle.css";
import "./fonts/fonts.css";
import Typo from "../Typo";

import ring from "./images/ring.jpg";
import engaged from "./images/engaged.jpg";
import crime from "./images/crime.jpg";
import family from "./images/family.jpg";

export default class DiamondArticle extends React.Component {
    constructor(props) {
        super(props);

        this.typos = {
            college: {
                default: "Colege",
                answer: "College",
            },
            evensong: {
                default: "Eversong",
                answer: "Evensong",
            },
            their: {
                default: "there",
                answer: "their",
            },
            children: {
                default: "childen",
                answer: "children",
            },
            pauls: {
                default: "Paul",
                answer: "Pauls",
            },
            detected: {
                default: "detective",
                answer: "detected",
            },
            snuck: {
                default: "snack",
                answer: "snuck",
            },
            ring: {
                default: "Thing",
                answer: "Ring",
            },
            who: {
                default: "whom",
                answer: "who",
            },
            cambridge: {
                default: "Cambriage",
                answer: "Cambridge",
            },
            to: {
                default: "too",
                answer: "to",
            },
            intelligence: {
                default: "intelligent",
                answer: "intelligence",
            },
            angeles: {
                default: "Angles",
                answer: "Angeles",
            },
            arrest: {
                default: "arest",
                answer: "arrest",
            },
            supposedly: {
                default: "suppposedly",
                answer: "supposedly",
            },
        };

        this.state = {
            typosLeft: Object.keys(this.typos).length,
        };

        this.typoFactory = this.typoFactory.bind(this);
        this.typoFixed = this.typoFixed.bind(this);
    }

    typoFactory(typoObject) {
        var startText = this.props.typosOmitted
            ? typoObject.answer
            : typoObject.default;

        return (
            <Typo answer={typoObject.answer} onFixed={this.typoFixed}>
                {startText}
            </Typo>
        );
    }

    typoFixed() {
        var typosLeft = this.props.typosOmitted ? 0 : this.state.typosLeft - 1;
        this.setState({
            typosLeft: typosLeft,
        });
    }

    componentDidUpdate(prevProps, prevState) {
        if (
            this.state.typosLeft === 0 &&
            this.state.typosLeft !== prevState.typosLeft &&
            this.props.onAllTyposFixed
        ) {
            this.props.onAllTyposFixed(this.props.articleId);
        }
    }

    render() {
        return (
            <div
                className="diamond article"
                style={{ backgroundColor: "linen" }}
            >
                <div className="diamond mockSite">
                    <div className="diamond topBanner">
                        <div className="diamond siteTitleContainer">
                            <div className="diamond siteTitle">
                                The King's{" "}
                                {this.typoFactory(this.typos.college)} Crier
                            </div>
                            <div className="diamond siteSubtitle">
                                <span className="diamond sitesubtitleText">
                                    est. 1562 A.D.
                                </span>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="diamond contentBanner">
                    <p>
                        World | Britain | Politics | Opinion |{" "}
                        {this.typoFactory(this.typos.evensong)}
                    </p>
                </div>

                <div className="diamond articleHeader">
                    <img
                        className="diamond headerImage"
                        src={ring}
                        alt="ring"
                    />
                    <div className="diamond articleTitle">
                        <h1>
                            Diamond Thief Strikes Crown Jewels; Accomplice
                            Identified
                        </h1>
                    </div>
                    <div className="diamond articleSubtitle">
                        <h2>
                            Spurred on by his heart rather than his head, the
                            Plaid Pickpocket has committed his most brazen act
                            yet.
                        </h2>
                    </div>

                    <div className="diamond articleAttribution">
                        <p>
                            <i>
                                <u>Kade Fiddleton, KCC London Correspondent</u>
                                <br />
                                March 6th, 2020
                            </i>
                        </p>
                    </div>
                    <div style={{ clear: "both" }}></div>
                </div>

                <div className="diamond articleContent">
                    <p>
                        The Tower of London revealed today that the Gaudium
                        Ring, part of the Crown Jewels collection and valued at
                        £18,000,000, has been stolen. The discovery came after
                        the ring was seen posted in pictures on social media, in
                        which American woman Cambria Stame can be seen wearing
                        the ring. The museum curators discovered soon after that
                        the Gaudium Ring currently in{" "}
                        {this.typoFactory(this.typos.their)} care is a fake, the
                        real one having been stolen nearly a year and a half
                        ago. Images of Cambria Stame reveal her association with
                        Noah Pauls, otherwise known as the Plaid Pickpocket, who
                        is currently the prime suspect in the case.
                    </p>
                    <div className="diamond leftImage">
                        <img
                            className="diamond image"
                            src={crime}
                            alt="crime"
                        />
                        <div className="diamond imageCaption">
                            <div className="diamond imageCaptionText">
                                Cambria Stame, the recipient of the Gaudium
                                Ring, seen here attacking{" "}
                                {this.typoFactory(this.typos.children)}.
                            </div>
                        </div>
                    </div>
                    <p>
                        An initial police investigation revealed that{" "}
                        {this.typoFactory(this.typos.pauls)} visited London in
                        early December of 2018, shortly after he first made
                        contact with Ms. Stame. Further review of security
                        footage from the Crown Jewels exhibit shows Pauls taking
                        a single ride on the moving walkway around the jewels,
                        during which it is assumed the ring was swapped out.
                        However, his methodology for making the swap is yet
                        unclear. “The glass containing the Crown Jewels is
                        designed to be bulletproof, with a laser protection
                        system in place on the inside as well,” said Edward
                        Glasby, chief of London Police. “It seems totally
                        impossible to make a swap without being{" "}
                        {this.typoFactory(this.typos.detected)}, let alone in
                        the middle of a crowd of onlookers.”
                    </p>
                    <p>
                        MI6 has joined the investigation, and recently released
                        a report stating that the ring was transported out of
                        the country in the baggage of Ms. Stame, who had been
                        studying in Cambridge and was returning to America
                        around the time of the Plaid Pickpocket’s burglary. “We
                        believe she was used as a mule without her knowledge,”
                        the statement read. “Pauls most likely{" "}
                        {this.typoFactory(this.typos.snuck)} the ring into her
                        checked bag at the airport; Ms. Stame, however, did not
                        see this occur.” Security footage from the airport is
                        still being reviewed.
                    </p>
                    <div className="diamond articleQuote">
                        <div className="diamond articleQuoteText">
                            MI6: Cambria Stame unwittingly transported the
                            Gaudium {this.typoFactory(this.typos.ring)} out of
                            the country.
                        </div>
                    </div>
                    <p>
                        The burglary was only discovered today, nearly a year
                        and a half after it occurred, when the Gaudium Ring was
                        spotted being worn by Cambria Stame in pictures posted
                        to social media. The pictures show Ms. Stame wearing the
                        ring after being proposed to by Pauls,{" "}
                        {this.typoFactory(this.typos.who)} allegedly used the
                        Gaudium Ring as an engagement ring. It is unclear
                        whether Ms. Stame is aware of the origin of the ring.
                    </p>
                    <div className="diamond rightImage">
                        <img
                            className="diamond image"
                            src={engaged}
                            alt="engaged"
                        />
                        <div className="diamond imageCaption">
                            <div className="diamond imageCaptionText">
                                The Gaudium Ring clearly visible in this
                                engagement photo, which was posted to social
                                media earlier today.
                            </div>
                        </div>
                    </div>
                    <p>
                        The Duchess of {this.typoFactory(this.typos.cambridge)},
                        who wore the ring during her wedding to Prince William,
                        was asked to comment on the ring’s burglary. Her reply
                        was unexpectedly positive. “I have seen the pictures,
                        and am quite happy for Ms. Stame,” she said to the
                        press. “The ring looks absolutely wonderful on her. I
                        send my congratulations to her on her engagement, and I
                        hope I will be lucky enough to receive an invitation{" "}
                        {this.typoFactory(this.typos.to)} her wedding. I will
                        make my attendance there a top priority.”
                    </p>

                    <div className="diamond articleQuote">
                        <div className="diamond articleQuoteText">
                            Kate Middleton promises to attend Cambria Stame's
                            wedding if invited.
                        </div>
                    </div>
                    <p>
                        The London Police are currently seeking to extradite
                        Pauls to the United Kingdom. They have hit a snag,
                        however, after{" "}
                        {this.typoFactory(this.typos.intelligence)} suggested
                        that Pauls is currently hiding out in Idaho, the
                        location of which is unknown. “We have never heard of
                        this ‘Idaho’ place,” Head of Police Glasby said. The
                        United States was previously thought to have only three
                        cities: New York, Boston, and Los{" "}
                        {this.typoFactory(this.typos.angeles)}. Some suggest
                        that Idaho may be a new sovereign nation that Pauls
                        created to avoid extradition, although MI6 has not
                        confirmed these rumors.
                    </p>
                    <p>
                        Pauls is currently wanted in 18 countries for burglaries
                        totaling £2 billion in value, and a reward of £300,000
                        is available for anyone with information that leads to
                        his {this.typoFactory(this.typos.arrest)} and the return
                        of the Gaudium Ring. If you know anything that may lead
                        to his capture, please call the London Police at (020)
                        799 529.
                    </p>
                    <div className="diamond bigImage">
                        <img
                            className="diamond image"
                            src={family}
                            alt="family"
                        />
                        <div className="diamond imageCaption">
                            <div className="diamond imageCaptionText">
                                Noah Pauls, Cambria Stame, and their families
                                are hiding out in "Idaho",{" "}
                                {this.typoFactory(this.typos.supposedly)}{" "}
                                located in the United States.
                            </div>
                        </div>
                    </div>
                </div>

                <div className="diamond articleFooter">
                    <div className="diamond articleCopyright">
                        <p>
                            Copyright &copy; 2020 The King's College Crier.
                            <br />
                            The King's College Crier is not responsible for the
                            content of external sites.
                        </p>
                    </div>
                    <div className="diamond articleTyposLeft">
                        <p>
                            {this.state.typosLeft === 0
                                ? "Article finished!"
                                : "Keep searching."}{" "}
                            Typos Remaining: {this.state.typosLeft}
                        </p>
                    </div>
                    <Link to="/dashboard" onClick={() => window.scrollTo(0, 0)}>
                        Return to Dashboard
                    </Link>
                </div>
            </div>
        );
    }
}
