import React from "react";
import { Link } from "react-router-dom";

import "./Finale.css";
import "./fonts/fonts.css";
import Typo from "../Typo";

import laugh from "./images/laugh.jpg";
import tulips from "./images/tulips.jpg";
import beach from "./images/beach.jpg";
import name from "./images/name.png";

export default class Finale extends React.Component {
    constructor(props) {
        super(props);
        this.typos = {
            cambria:{
                default: "Cambira",
                answer: "Cambria"
            },
            thievery: {
                default: "theifery",
                answer: "thievery",
            },
            smile: {
                default: "smlie",
                answer: "smile",
            },
            love: {
                default: "luh",
                answer: "love",
            },
            pauls: {
                default: "Stame",
                answer: "Pauls",
            },
        };

        this.state = {
            typosLeft: Object.keys(this.typos).length,
        };

        this.typoFactory = this.typoFactory.bind(this);
        this.typoFixed = this.typoFixed.bind(this);
    }

    typoFactory(typoObject) {
        var startText = this.props.typosOmitted
            ? typoObject.answer
            : typoObject.default;

        return (
            <Typo answer={typoObject.answer} onFixed={this.typoFixed}>
                {startText}
            </Typo>
        );
    }

    typoFixed() {
        var typosLeft = this.props.typosOmitted ? 0 : this.state.typosLeft - 1;
        this.setState({
            typosLeft: typosLeft,
        });
    }

    componentDidUpdate(prevProps, prevState) {
        if (
            this.state.typosLeft === 0 &&
            this.state.typosLeft !== prevState.typosLeft &&
            this.props.onAllTyposFixed
        ) {
            this.props.onAllTyposFixed(this.props.articleId);
        }
    }

    render() {
        return (
            <div className="letter">
                <div className="spacer" />
                <p>Dearest {this.typoFactory(this.typos.cambria)},</p>
                {this.partOne()}
                {this.partTwo()}
                {this.partThree()}
                {this.partFourt()}
                {this.valediction()}
                <div className="spacer" />
            </div>
        );
    }

    partOne() {
        if (this.state.typosLeft <= 4) {
            return (
                <div>
                    <p>
                        The four articles on this website, containing many of my
                        favorite memories from the past six months, are mere
                        flashes of just how wonderful it always is to be your
                        partner in crime. Even if the crime is royal diamond{" "}{this.typoFactory(this.typos.thievery)}.
                    </p>
                </div>
            );
        } else {
            return null;
        }
    }

    partTwo() {
        if (this.state.typosLeft <= 3) {
            return (
                <div>
                    <img src={laugh} alt="laugh" />
                    <p>
                        I don’t have much I’d like to say here, except that
                        every single one of those articles is written about
                        times when you made me smile uncontrollably over the
                        fact that you, incredible Cambria Stame, were my
                        girlfriend, are now my fiancée, and will soon be my
                        wife. I only hope that this website has made you {this.typoFactory(this.typos.smile)} a
                        little bit yourself.
                    </p>
                </div>
            );
        } else {
            return null;
        }
    }

    partThree() {
        if (this.state.typosLeft <= 2) {
            return (
                <div>
                    <img src={tulips} alt="tulips" />
                    <p>
                        Really, that is one of the greatest joys in my life:
                        knowing that my sole mission from here on out is to make
                        you smile as often as I possibly can. It won’t always be
                        easy; the times we live in prove that life together will
                        not be free of worry and anxiety over what the present
                        and future holds. But no matter when or where we end up,
                        I hope to always be a reliable source of selfless {this.typoFactory(this.typos.love)} for you.
                    </p>
                </div>
            );
        } else {
            return null;
        }
    }

    partFourt() {
        if (this.state.typosLeft <= 1) {
            return (
                <div>
                    <img src={beach} alt="beach" />
                    <p>
                        There’s just one last typo on this page. I’ll even give
                        it away: it’s in the last sentence of this letter. To
                        correct it, fast-forward your mind to the late afternoon
                        on September 5th, 2021.
                    </p>
                    <p>
                        I love you, Cambria {this.typoFactory(this.typos.pauls)}
                        .
                    </p>
                </div>
            );
        } else {
            return null;
        }
    }

    valediction() {
        if (this.state.typosLeft <= 0) {
            return (
                <div>
                    <p>Happy Eighteen Months!</p>
                    <div className="valediction">
                        <p>Yours always,</p>
                        <img src={name} alt="Noah" />
                    </div>
                    <div style={{ clear: "both" }}></div>
                    <div className="spacer" />
                    <div className="spacer" />
                    <div className="linkContainer">
                        <Link to="/dashboard">Return to Dashboard</Link>
                    </div>
                </div>
            );
        } else {
            return (
                <div>
                    <div className="spacer" />
                    <div className="spacer" />
                </div>
            );
        }
    }
}
