import React from 'react';
import './Typo.css';

export default class MultiAnswerTypo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isFixed: this.props.answers.includes(this.props.children),
            currentText: this.props.children
        };

        this.handleInput = this.handleInput.bind(this);

        if (this.state.isFixed && this.props.onFixed) {
            this.props.onFixed(this.state.currentText);
        }
    }

    handleInput(event) {
        const userInput = event.currentTarget.textContent;
        const fixed = this.props.answers.includes(userInput);
        this.setState({
            isFixed: fixed,
            currentText: userInput
        });
    }

    componentDidUpdate(prevProps, prevState) {
        // check to make sure update only happens on change to isFixed
        if (this.state.isFixed && !prevState.isFixed && this.props.onFixed) { 
            this.props.onFixed(this.state.currentText);
        }
    }

    render() {
        return (
            <span contentEditable={!this.state.isFixed} 
                spellcheck="false"
                onInput={this.handleInput} 
                suppressContentEditableWarning={true}
            >
                {this.props.children}
            </span>
        );
    }
}
