import React from 'react';
import './Typo.css';

// For getting contentEditable to work: https://stackoverflow.com/a/52614631
// For suppressing contentEditable warnings from React: https://stackoverflow.com/q/49639144/11898061
export default class Typo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isFixed: this.props.children === this.props.answer
        };

        this.handleInput = this.handleInput.bind(this);

        if (this.state.isFixed && this.props.onFixed) {
            this.props.onFixed();
        }
    }

    handleInput(event) {
        const userInput = event.currentTarget.textContent;
        const fixed = this.props.answer === userInput;
        this.setState({
            isFixed: fixed
        });
    }

    componentDidUpdate(prevProps, prevState) {
        // check to make sure update only happens on change to isFixed
        if (this.state.isFixed && !prevState.isFixed && this.props.onFixed) { 
            this.props.onFixed();
        }
    }

    render() {
        return (
            <span contentEditable={!this.state.isFixed} 
                spellcheck="false"
                tabindex="-1"
                onInput={this.handleInput} 
                suppressContentEditableWarning={true}
            >
                {this.props.children}
            </span>
        );
    }
}
