import React from "react";
import { Link } from "react-router-dom";

import "./CaliforniaArticle.css";
import "./fonts/fonts.css";
import Typo from "../Typo";

import castle from "./images/castle.jpg";
import disney_falcon from "./images/disney_falcon.jpg";
import disney_splash from "./images/disney_splash.jpg";
import disney_excited from "./images/disney_excited.jpg";
import couple from "./images/couple.jpg";

export default class CaliforniaArticle extends React.Component {
    constructor(props) {
        super(props);

        this.typos = {
            your: {
                default: "you're",
                answer: "your",
            },
            place: {
                default: "Pace",
                answer: "Place",
            },
            dollars: {
                default: "dollers",
                answer: "dollars",
            },
            bills: {
                default: "bill",
                answer: "bills",
            },
            land: {
                default: "Hand",
                answer: "Land",
            },
            expenditure: {
                default: "expentudire",
                answer: "expenditure",
            },
            spend: {
                default: "spenb",
                answer: "spend",
            },
            fantasmic: {
                default: "Fantastic",
                answer: "Fantasmic",
            },
            ate: {
                default: "at",
                answer: "ate",
            },
            borabora: {
                default: "Bore Bore",
                answer: "Bora Bora",
            },
        };

        this.state = {
            typosLeft: Object.keys(this.typos).length,
        };

        this.typoFactory = this.typoFactory.bind(this);
        this.typoFixed = this.typoFixed.bind(this);
    }

    typoFactory(typoObject) {
        var startText = this.props.typosOmitted
            ? typoObject.answer
            : typoObject.default;

        return (
            <Typo answer={typoObject.answer} onFixed={this.typoFixed}>
                {startText}
            </Typo>
        );
    }

    typoFixed() {
        var typosLeft = this.props.typosOmitted ? 0 : this.state.typosLeft - 1;
        this.setState({
            typosLeft: typosLeft,
        });
    }

    componentDidUpdate(prevProps, prevState) {
        if (
            this.state.typosLeft === 0 &&
            this.state.typosLeft !== prevState.typosLeft &&
            this.props.onAllTyposFixed
        ) {
            this.props.onAllTyposFixed(this.props.articleId);
        }
    }

    render() {
        return (
            <div
                className="cali article"
                style={{ backgroundColor: "whitesmoke" }}
            >
                <div className="cali mockSite">
                    <div className="cali topBanner">
                        <div className="cali siteTitleContainer">
                            <div className="cali siteTitle">
                                Tod's Travel Blog
                            </div>
                            <div className="cali siteSubtitle">
                                Staying home is a fool's errand.
                            </div>
                        </div>
                    </div>
                </div>

                <div className="cali contestBanner">
                    <p>
                        <span style={{ fontFamily: "Raleway Bold" }}>
                            Tod's Top Travel Blog 2020:
                        </span>{" "}
                        Submit {this.typoFactory(this.typos.your)} blog today
                        for a chance to win a dream vacation to Bora Bora!
                    </p>
                </div>

                <div className="cali articleHeader">
                    <div className="cali articleTitle">
                        <h1>California Adventure</h1>
                    </div>
                    <div className="cali articleSubtitle">
                        <h2>
                            Disneyland proves that the face of consumerism is
                            that of Mickey Mouse.
                        </h2>
                    </div>
                    <img
                        className="cali headerImage"
                        src={castle}
                        alt="castle"
                    />

                    <div className="cali articleAttribution">
                        <p>
                            <i>Published February 7th, 2020 by Tod Meyerston</i>
                        </p>
                    </div>
                </div>

                <div className="cali articleContent">
                    <hr />
                    <p>
                        Love. Beauty. Happiness. Prepare to say goodbye to these
                        feelings and emotions as you walk through the entrance
                        of Disneyland in Orange County, California. I’m Tod
                        Meyerston, and today I will be uncovering the truth
                        about the self-proclaimed “Happiest{" "}
                        {this.typoFactory(this.typos.place)} on Earth”. Though
                        Disneyland claims to make dreams come true, my recent
                        experience at the park proves that dreams of frugality
                        go there to die a slow, painful death.
                    </p>
                    <img
                        className="cali rightImage"
                        src={disney_excited}
                        alt="disney excited"
                    />
                    <p>
                        The Disney suffering began soon after exiting the park’s
                        parking superstructure (an absurd expense just to park a
                        car, mind you). Having passed through a security
                        checkpoint, I soon entered a line of people waiting to
                        board what I thought was a bus to the park. Except that
                        it was not a bus at all: instead, guests were shuttled
                        via a most ridiculous tram! Each individual car of the
                        tram could steer itself, and the technology required to
                        develop and deploy the thing must have been in the tens
                        of millions of {this.typoFactory(this.typos.dollars)}.
                        Disgusted, I asked several attendants if they would
                        allow me to walk to the park entrance, but they told me
                        that the tram was the only way. I reluctantly boarded,
                        dreading what this traumatic experience meant for the
                        rest of my day at Disneyland.
                    </p>
                    <p>
                        Once off of that ludicrous little tram, I soon passed
                        through the front gate of Disneyland. Upon entering, my
                        jaw dropped through the ground to the molten core of the
                        earth. Elaborate flower displays. A train station with a
                        functioning train. A horse-drawn carriage. I had to keep
                        myself from vomiting at the extravagance. I began a slow
                        walk down “Main Street USA”, which I soon discovered was
                        a monument to materialism. Over-priced silhouette
                        portraits. Silly Christmas ornaments. Diamond-encrusted
                        castles. Anything that people could possibly be tricked
                        into buying was being sold here, and I was in the midst
                        of a huge crowd of poor souls deceived by clever
                        marketing that began the moment they first watched a
                        Disney animation. Musical groups sang and played songs
                        up and down the street, most likely hoping that
                        park-goers would simply throw hundred-dollar{" "}
                        {this.typoFactory(this.typos.bills)} at them when the
                        show was over. Luckily, all that the performers got was
                        a measly round of applause afterwards. Serves them
                        right.
                    </p>
                    <img
                        className="cali bigImage"
                        src={disney_falcon}
                        alt="disney_falcon"
                    />
                    <p>
                        Perhaps I might be more understanding of the current
                        state of Disneyland if their insistence on luxury was
                        simply a matter of nostalgia. Maybe I could look the
                        other way if all of the unnecessary technology packed
                        into every one of their outrageous rides was all a
                        remembrance of the park’s past and nothing more. But as
                        soon as I entered the so-called “Star Wars{" "}
                        {this.typoFactory(this.typos.land)}”, I knew exactly
                        what Disney wanted to do: spend as much money as could
                        possibly be spent creating a monument to lavish{" "}
                        {this.typoFactory(this.typos.expenditure)}. Stands sold
                        blue milk to children whose minds were slowly being
                        turned to the ideology of materialism. Adults infected
                        by the disease of consumption bought robots and
                        lightsabers believing them to be relics of a pop culture
                        temple. Worst of all, the massive “Millennium Falcon”, a
                        fake spaceship incapable of flying, captured the gazes
                        of once innocent bystanders, combusting their sense of
                        financial decency into an innate desire to spend,{" "}
                        {this.typoFactory(this.typos.spend)}, spend. I was so
                        sick watching these sites that I could hardly walk.
                    </p>
                    <img
                        className="cali leftImage"
                        src={disney_splash}
                        alt="disney_splash"
                    />
                    <p>
                        The tragedy of “Star Wars Land” helped me to see
                        everything at Disneyland for what it truly was. Splash
                        Mountain? A way to convince guests that Disney’s
                        spending on ride safety was all that guaranteed their
                        security in this world.{" "}
                        {this.typoFactory(this.typos.fantasmic)}? Another
                        performance to make park guests believe in Disney as
                        their religion by propping up Mickey Mouse as some sort
                        of savior of mankind. And Small World? Nothing less than
                        an infectious song and dance designed to penetrate one’s
                        deepest sense of goodness and truth, replacing it with
                        memories of Disney’s dancing child figurines. Finally, I
                        saw the real Disneyland: nothing is truth, everything is
                        deception.
                    </p>
                    <p>
                        So, does Disneyland get the Tod Meyerston seal of
                        approval? Yes. The churros at Disneyland are to die for.
                        No matter how disgusted I was by the most recent ride I
                        went on, I could always count on the fact that waiting
                        for me outside the ride exit would be a warm, chewy
                        stick of pure sugar and cinnamon. During my twelve hours
                        at the park, I {this.typoFactory(this.typos.ate)} nearly
                        twenty such churros, each one better than the last. Upon
                        exiting the park (ten pounds heavier than when I
                        entered), I immediately bought the highest tier annual
                        pass I could possibly purchase. That’s only $3,000 for
                        unlimited access to Disneyland churros all year long! A
                        total bargain, if I do say so myself. Hooray for
                        Disneyland, truly the Happiest Place on Earth.
                    </p>
                    <img className="cali bigImage" src={couple} alt="couple" />
                    <p>
                        <i>
                            Follow my blog to be updated when I write about my
                            upcoming trip to Disneyworld in Florida, which I
                            booked tickets for immediately after hearing that
                            they too have churros! And if you’re a travel
                            blogger yourself, be sure to enter your blog into my
                            travel blog competition for a chance to win a
                            vacation to {this.typoFactory(this.typos.borabora)}.
                        </i>
                    </p>
                </div>

                <div className="cali articleFooter">
                    <div className="cali articleCopyright">
                        <p>
                            Copyright &copy; 2020 Tod Meyerston. All rights
                            reserved.
                        </p>
                    </div>
                    <div className="cali articleTyposLeft">
                        <p>
                            {this.state.typosLeft === 0
                                ? "Article finished!"
                                : "Keep searching."}{" "}
                            Typos Remaining: {this.state.typosLeft}
                        </p>
                    </div>
                    <Link to="/dashboard" onClick={() => window.scrollTo(0, 0)}>
                        Return to Dashboard
                    </Link>
                </div>
            </div>
        );
    }
}
