import React from "react";
import { Link } from "react-router-dom";

import "./Dashboard.css";

export default class Dashboard extends React.Component {
    constructor(props) {
        super(props);
        this.links = [
            {
                articleId: "newyear",
                to: "/newyear",
                text: "New Year",
                predecessors: [],
            },
            {
                articleId: "california",
                to: "/california",
                text: "California Adventure",
                predecessors: ["newyear"],
            },
            {
                articleId: "diamond",
                to: "/diamond",
                text: "Diamond Thief",
                predecessors: ["california"],
            },
            {
                articleId: "domestic",
                to: "/domestic",
                text: "Domestic Affairs",
                predecessors: ["diamond"],
            },
            {
                articleId: "finale",
                to: "/finale",
                text: "Eighteen Months",
                predecessors: ["domestic"],
            }
        ];
        this.state = {};

        this.predecessorsComplete = this.predecessorsComplete.bind(this);
    }

    predecessorsComplete(linkObject) {
        if (!this.props.playGame) {return true}
        var areComplete = true;
        for (var i=0; i<linkObject.predecessors.length; i++) {
            areComplete = areComplete && this.props.completedArticles.includes(linkObject.predecessors[i]);
        }
        return areComplete;
    }

    render() {
        return (
            <div className="dashboard">
                {this.links.map((link) => {
                    return (
                        (this.predecessorsComplete(link))
                        ? <div className={"linkContainer " + link.articleId} key={link.articleId}>
                            <Link to={link.to}
                            onClick={() => window.scrollTo(0, 0)}>
                                {link.text}
                            </Link>
                        </div>
                        : null
                    );
                })}
            </div>
        );
    }
}
